import axios from 'axios'
import Vue from 'vue'

export function request(config){
  // 1.创建axios的实例
  const instance = axios.create({
    baseURL: 'http://www.softeem.xin/maoyanApi/ajax/',
    //baseURL: 'http://106.54.54.237:8000/api/v1,
    timeout: 5000
  })

  // 2.axios的拦截器
  // 2.1.请求拦截的作用
  instance.interceptors.request.use(config => {
    // console.log("axios 拦截器 request");
    Vue.prototype.$loading.showLoading();
    return config;
  }, err => {
    console.log(err);
  })

  // 2.2.响应拦截
  instance.interceptors.response.use(res => {
    Vue.prototype.$loading.closeLoading();
    // console.log("axios 拦截器 response");
    return res.data;
  }, err => {
    console.log(err);
  })

  // 3.发送真正的网络请求
  return instance(config)
}